
const _ = require('lodash');

export function gamesByOwnerFunc (games) {
  //get games by owner
  const gamesByOwners = [];
  games.forEach((game) => {
    const teamA = game?.team_a[0];
    const teamB = game?.team_b[0];
    let teamObject = [];
    if (gamesByOwners.find(obj => obj.id === teamA?.ownerId)) {
        teamObject = gamesByOwners.find(obj => obj.id === teamA?.ownerId);
        teamObject.games.push(game);
    } else {
      gamesByOwners.push({
        id: teamA?.ownerId,
        name: teamA?.owner,
        games: [game]
      })
    }
    if (gamesByOwners.find(obj => obj.id === teamB?.ownerId)) {
        teamObject = gamesByOwners.find(obj => obj.id === teamB?.ownerId);
        teamObject.games.push(game);
    } else {
      gamesByOwners.push({
        id: teamB?.ownerId,
        name: teamB?.owner,
        games: [game]
      })
    }
  });

  return gamesByOwners;
}

export function gameOutcome(game,owners) {

  let winner, winnerId, loser, loserId, scoreWin, scoreLose;
  winner = winnerId = loser = loserId = '';
  scoreWin = scoreLose = 0;
  if (parseFloat(game.team_a["0"].score) > parseFloat(game.team_b["0"].score)) {
    winnerId = game.team_a["0"].ownerId;
    scoreWin = game.team_a["0"].score;
    loserId = game.team_b["0"].ownerId;
    scoreLose = game.team_b["0"].score;
    winner = _.find(owners, ['id', winnerId]).name;
    loser = _.find(owners, ['id', loserId]).name;
  }
  else if (parseFloat(game.team_b["0"].score) > parseFloat(game.team_a["0"].score)) {
    winnerId = game.team_b["0"].ownerId;
    scoreWin = game.team_b["0"].score;
    loserId = game.team_a["0"].ownerId;
    scoreLose = game.team_a["0"].score;
    winner = _.find(owners, ['id', winnerId]).name;
    loser = _.find(owners, ['id', loserId]).name;
  }

  if (winner !== '') {
    return {
      winner: winner,
      loser: loser,
      winnerId: winnerId,
      loserId: loserId,
      scoreWin: scoreWin,
      scoreLose: scoreLose,
      week: game.week,
      year: game.year,
      margin: parseFloat((scoreWin - scoreLose).toFixed(2)),
      sumScore: parseFloat((scoreWin + scoreLose).toFixed(2))
    };
  }
}

export function minus(a, b) {
  return a - b;
}

export function multiply(a, b) {
  return a * b;
}

export function divide(a, b) {
  return a / b;
}